import { BaseRoutes } from '../../../routes/baseRoutes';

import { addDrug, updateDrug, getDrugs, deleteDrug } from '../controllers';

class DrugsRoutes extends BaseRoutes {
  public constructor() {
    super({ mergeParams: true });
    this.routes();
  }

  public routes(): void {
    this.router.post('/drugs', [], addDrug);
    this.router.put('/drugs/:id', [], updateDrug);
    this.router.get('/drugs', [], getDrugs);
    this.router.delete('/drugs/:ids', [], deleteDrug);
  }
}

export { DrugsRoutes };
